"use client";

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Badge } from '@/components/ui/badge';
import { toast } from 'sonner';

const medications = [
  { name: 'Paracétamol 500mg', price: 120, category: 'Antalgique' },
  { name: 'Ibuprofène 400mg', price: 180, category: 'Anti-inflammatoire' },
  { name: 'Amoxicilline 500mg', price: 350, category: 'Antibiotique' },
  { name: 'Oméprazole 20mg', price: 280, category: 'Gastro-entérologie' },
  { name: 'Metformine 500mg', price: 220, category: 'Diabète' },
  { name: 'Losartan 50mg', price: 320, category: 'Cardiologie' },
  { name: 'Simvastatine 20mg', price: 290, category: 'Cardiologie' },
  { name: 'Salbutamol Inhalateur', price: 450, category: 'Respiratoire' }
];

const wilayas = [
  'Adrar', 'Chlef', 'Laghouat', 'Oum El Bouaghi', 'Batna', 'Béjaïa', 'Biskra', 'Béchar',
  'Blida', 'Bouira', 'Tamanrasset', 'Tébessa', 'Tlemcen', 'Tiaret', 'Tizi Ouzou', 'Alger',
  'Djelfa', 'Jijel', 'Sétif', 'Saïda', 'Skikda', 'Sidi Bel Abbès', 'Annaba', 'Guelma',
  'Constantine', 'Médéa', 'Mostaganem', 'MSila', 'Mascara', 'Ouargla', 'Oran', 'El Bayadh',
  'Illizi', 'Bordj Bou Arréridj', 'Boumerdès', 'El Tarf', 'Tindouf', 'Tissemsilt', 'El Oued',
  'Khenchela', 'Souk Ahras', 'Tipaza', 'Mila', 'Aïn Defla', 'Naâma', 'Aïn Témouchent',
  'Ghardaïa', 'Relizane'
];

const calculateDeliveryFee = (wilaya: string, isUrgent: boolean = false) => {
  const baseFee = 200; // Base fee in DA
  let fee = baseFee;
  
  // Major cities have lower fees
  const majorCities = ['Alger', 'Oran', 'Constantine', 'Annaba', 'Blida', 'Sétif', 'Batna'];
  if (majorCities.includes(wilaya)) {
    fee = 150;
  }
  
  // Remote areas have higher fees
  const remoteAreas = ['Tamanrasset', 'Adrar', 'Illizi', 'Tindouf', 'El Oued', 'Ouargla'];
  if (remoteAreas.includes(wilaya)) {
    fee = 400;
  }
  
  // Urgent delivery doubles the fee
  if (isUrgent) {
    fee *= 2;
  }
  
  return fee;
};

export default function MedicationDeliveryPage() {
  const [formData, setFormData] = useState({
    patientName: '',
    phone: '',
    email: '',
    wilaya: '',
    address: '',
    isUrgent: false,
    prescription: '',
    notes: ''
  });
  const [selectedMedications, setSelectedMedications] = useState<Array<{medication: typeof medications[0], quantity: number}>>([]);
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const deliveryFee = formData.wilaya ? calculateDeliveryFee(formData.wilaya, formData.isUrgent) : 0;
  const subtotal = selectedMedications.reduce((sum, item) => sum + (item.medication.price * item.quantity), 0);
  const total = subtotal + deliveryFee;

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.patientName.trim()) {
      newErrors.patientName = 'Le nom est requis';
    }
    if (!formData.phone.trim()) {
      newErrors.phone = 'Le numéro de téléphone est requis';
    }
    if (!formData.email.trim()) {
      newErrors.email = 'L\'email est requis';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Format d\'email invalide';
    }
    if (!formData.wilaya) {
      newErrors.wilaya = 'Veuillez sélectionner votre wilaya';
    }
    if (!formData.address.trim()) {
      newErrors.address = 'L\'adresse complète est requise';
    }
    if (selectedMedications.length === 0) {
      newErrors.medications = 'Veuillez ajouter au moins un médicament';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);
    
    try {
      // Simulation d'une API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      toast.success('Commande confirmée !', {
        description: `Votre commande de ${total} DA sera livrée dans les plus brefs délais.`
      });
      
      // Reset form
      setFormData({
        patientName: '',
        phone: '',
        email: '',
        wilaya: '',
        address: '',
        isUrgent: false,
        prescription: '',
        notes: ''
      });
      setSelectedMedications([]);
    } catch (error) {
      toast.error('Erreur lors de la commande', {
        description: 'Veuillez réessayer plus tard.'
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleInputChange = (field: string, value: string | boolean) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const addMedication = (medication: typeof medications[0]) => {
    const existing = selectedMedications.find(item => item.medication.name === medication.name);
    if (existing) {
      setSelectedMedications(prev => 
        prev.map(item => 
          item.medication.name === medication.name 
            ? { ...item, quantity: item.quantity + 1 }
            : item
        )
      );
    } else {
      setSelectedMedications(prev => [...prev, { medication, quantity: 1 }]);
    }
    if (errors.medications) {
      setErrors(prev => ({ ...prev, medications: '' }));
    }
  };

  const updateQuantity = (medicationName: string, quantity: number) => {
    if (quantity <= 0) {
      setSelectedMedications(prev => prev.filter(item => item.medication.name !== medicationName));
    } else {
      setSelectedMedications(prev => 
        prev.map(item => 
          item.medication.name === medicationName 
            ? { ...item, quantity }
            : item
        )
      );
    }
  };

  return (
    <div className="min-h-screen py-12 px-4 sm:px-6 lg:px-8">
      <div className="container mx-auto max-w-6xl">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-foreground mb-4">
            Livraison de Médicaments
          </h1>
          <p className="text-lg text-muted-foreground">
            Commandez vos médicaments et recevez-les directement chez vous
          </p>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Form */}
          <div className="lg:col-span-2 space-y-6">
            {/* Patient Info */}
            <Card>
              <CardHeader>
                <CardTitle>Informations de Livraison</CardTitle>
                <CardDescription>
                  Renseignez vos coordonnées pour la livraison
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="patientName">Nom complet *</Label>
                    <Input
                      id="patientName"
                      value={formData.patientName}
                      onChange={(e) => handleInputChange('patientName', e.target.value)}
                      placeholder="Votre nom complet"
                    />
                    {errors.patientName && (
                      <p className="text-sm text-destructive">{errors.patientName}</p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="phone">Téléphone *</Label>
                    <Input
                      id="phone"
                      value={formData.phone}
                      onChange={(e) => handleInputChange('phone', e.target.value)}
                      placeholder="0555 123 456"
                    />
                    {errors.phone && (
                      <p className="text-sm text-destructive">{errors.phone}</p>
                    )}
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="email">Email *</Label>
                  <Input
                    id="email"
                    type="email"
                    value={formData.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                    placeholder="votre.email@exemple.com"
                  />
                  {errors.email && (
                    <p className="text-sm text-destructive">{errors.email}</p>
                  )}
                </div>

                <div className="grid md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="wilaya">Wilaya *</Label>
                    <Select
                      value={formData.wilaya}
                      onValueChange={(value) => handleInputChange('wilaya', value)}
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Choisir votre wilaya" />
                      </SelectTrigger>
                      <SelectContent>
                        {wilayas.map((wilaya) => (
                          <SelectItem key={wilaya} value={wilaya}>
                            {wilaya}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                    {errors.wilaya && (
                      <p className="text-sm text-destructive">{errors.wilaya}</p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label>
                      <input
                        type="checkbox"
                        checked={formData.isUrgent}
                        onChange={(e) => handleInputChange('isUrgent', e.target.checked)}
                        className="mr-2"
                      />
                      Livraison urgente (24h)
                    </Label>
                    {formData.isUrgent && (
                      <p className="text-sm text-muted-foreground">
                        Frais de livraison doublés pour la livraison urgente
                      </p>
                    )}
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="address">Adresse complète *</Label>
                  <Textarea
                    id="address"
                    value={formData.address}
                    onChange={(e) => handleInputChange('address', e.target.value)}
                    placeholder="Adresse complète avec numéro de rue, quartier..."
                    rows={3}
                  />
                  {errors.address && (
                    <p className="text-sm text-destructive">{errors.address}</p>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Medications */}
            <Card>
              <CardHeader>
                <CardTitle>Sélection des Médicaments</CardTitle>
                <CardDescription>
                  Choisissez les médicaments dont vous avez besoin
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid md:grid-cols-2 gap-4 mb-6">
                  {medications.map((medication) => (
                    <div key={medication.name} className="border rounded-lg p-4">
                      <div className="flex justify-between items-start mb-2">
                        <h4 className="font-medium">{medication.name}</h4>
                        <Badge variant="secondary">{medication.category}</Badge>
                      </div>
                      <p className="text-sm text-muted-foreground mb-3">
                        Prix: {medication.price} DA
                      </p>
                      <Button
                        size="sm"
                        onClick={() => addMedication(medication)}
                        className="w-full"
                      >
                        Ajouter
                      </Button>
                    </div>
                  ))}
                </div>

                {errors.medications && (
                  <Alert className="mb-4">
                    <AlertDescription className="text-destructive">
                      {errors.medications}
                    </AlertDescription>
                  </Alert>
                )}

                {selectedMedications.length > 0 && (
                  <div className="space-y-4">
                    <h4 className="font-medium">Médicaments sélectionnés:</h4>
                    {selectedMedications.map((item) => (
                      <div key={item.medication.name} className="flex items-center justify-between p-3 border rounded">
                        <div>
                          <p className="font-medium">{item.medication.name}</p>
                          <p className="text-sm text-muted-foreground">
                            {item.medication.price} DA × {item.quantity} = {item.medication.price * item.quantity} DA
                          </p>
                        </div>
                        <div className="flex items-center space-x-2">
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => updateQuantity(item.medication.name, item.quantity - 1)}
                          >
                            -
                          </Button>
                          <span className="w-8 text-center">{item.quantity}</span>
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => updateQuantity(item.medication.name, item.quantity + 1)}
                          >
                            +
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Additional Info */}
            <Card>
              <CardHeader>
                <CardTitle>Informations Complémentaires</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="prescription">Ordonnance (optionnel)</Label>
                  <Textarea
                    id="prescription"
                    value={formData.prescription}
                    onChange={(e) => handleInputChange('prescription', e.target.value)}
                    placeholder="Détails de l'ordonnance si applicable..."
                    rows={3}
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="notes">Notes pour le livreur</Label>
                  <Textarea
                    id="notes"
                    value={formData.notes}
                    onChange={(e) => handleInputChange('notes', e.target.value)}
                    placeholder="Instructions spéciales pour la livraison..."
                    rows={2}
                  />
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Order Summary */}
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Résumé de la Commande</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span>Sous-total:</span>
                    <span>{subtotal} DA</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Frais de livraison:</span>
                    <span>{deliveryFee} DA</span>
                  </div>
                  {formData.isUrgent && (
                    <p className="text-xs text-muted-foreground">
                      (Livraison urgente 24h)
                    </p>
                  )}
                  <div className="border-t pt-2">
                    <div className="flex justify-between font-bold">
                      <span>Total:</span>
                      <span>{total} DA</span>
                    </div>
                  </div>
                </div>

                <Button 
                  onClick={handleSubmit} 
                  className="w-full" 
                  disabled={isSubmitting || selectedMedications.length === 0}
                >
                  {isSubmitting ? 'Commande en cours...' : 'Confirmer la Commande'}
                </Button>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Informations de Livraison</CardTitle>
              </CardHeader>
              <CardContent className="space-y-2 text-sm">
                <p>🚚 Livraison standard: 2-3 jours</p>
                <p>⚡ Livraison urgente: 24h</p>
                <p>📞 Suivi par SMS et appel</p>
                <p>💳 Paiement à la livraison</p>
              </CardContent>
            </Card>

            <Alert>
              <AlertDescription>
                Nos coursiers sont formés pour manipuler les médicaments en toute sécurité. 
                Une pièce d'identité sera demandée à la livraison.
              </AlertDescription>
            </Alert>
          </div>
        </div>
      </div>
    </div>
  );
}
